import React from "react";
import { Link } from "react-router-dom";
import styles from './GoHome.module.css'

const GoHome = () => {
  // const handleClick = () => {};
  return (
    <div className={styles.box}>
      <p className={styles.text}>   </p>
      <Link to="/" className={styles.link}>   </Link>
    </div>
  );
};

export default GoHome;
